.contain-succ {
  display: block;
  position: relative;
  /* padding-left: 35px; */
  /* margin-bottom: 12px; */
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.contain-succ input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.succ-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 28px;
  width: 28px;
  border: 1px solid #dddee2;
  border-radius: 20px;
  background-color: #eee;
  align-items: center;
}

/* On mouse-over, add a grey background color */
.contain-succ:hover input~.succ-checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.contain-succ input:checked~.succ-checkmark {
  background-color: #77D08B;
}

/* Create the succ-checkmark/indicator (hidden when not checked) */
.succ-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the succ-checkmark when checked */
.contain-succ input:checked~.succ-checkmark:after {
  display: block;
}

/* Style the succ-checkmark/indicator */
.contain-succ .succ-checkmark:after {
  left: 10px;
  top: 5px;
  width: 6px;
  height: 13px;
  border: solid white;
  border-radius: 2px;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

