.payment-skle{
    width: 60%;
    margin: auto;
}
.payment-btn{
    display: none;
}

@media screen and (min-width: 1500px){
    .main-flx{
        width: 1200px;
    }
  }

  @media(max-width: 720px){
    .payment-skle{
        width: 100%;
        margin: auto;
    }
    .payment-btn{
        display: block;
        position: fixed;
        width: 100%;
        bottom: 0px;
        background-color: white;
        height: 80px;
        padding-top: 20px;
        
    }
  }