.moveUpHmeCls{
  margin-top: -20px;
}

.HomePagePrntCls .loader-container { 
    width: 100%;
    height: 100%;
  }

.lineBtwnHmPgCls{
  opacity: 0.2;
  border: 1px solid rgba(9, 28, 63, 0.4);
}
.backLoadGray{
  position: fixed;
  width: 100%;
  z-index: 2;
  height: 100%;
  background-color: #fff;
  // background-color: rgb(0,0,0);
  // background-color: rgba(0,0,0, 0.4);
  color: white;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loaderGlobalCenter{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.more-BoxShadow{
  box-shadow: 0px 10px 30px rgba(94, 125, 182, 0.1) !important;
}

@media(max-width: 800px){
  .lineBtwnHmPgCls{
    margin-bottom: 40px;
  }
}


