body {
  counter-reset: sectionForDetails;                       /* Set a counter named 'section', and its initial value is 0. */
}
.inserNumB4Dtls::before{
  counter-increment: sectionForDetails;                   /* Increment the value of section counter by 1 */
  content: counter(sectionForDetails) ". ";
  padding-right: 13px;
}
.mobview{
