    height: 65px;
    color: #fff;
    font-size: 18px;
    margin-right: 10px;
    padding-top: 20px;
}

.BottomSheetShow{
    bottom: 0px;
}

.btnGetstatredBanner{
    -webkit-animation: color-blink 1s infinite;
            animation: color-blink 1s infinite;
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
    padding: 10px 30px;
    border: 0;
    border-radius: 20px;
    transition: all .2s;
}

.closeBanneridc{
    position: absolute;
    right: 0px;
    padding-right: 15px;
    padding-top: 20px;
}

.closeBanner{
    height: 20px;  
