.accflexx {
    display: flex;
    width: 100%;
    margin: auto;
}

.contentflex {
    width: 50%;
    // margin-left: 100px;
    padding-left: 100px;
}
.wrapp{
    float: right;
    width: 360px;
    margin-right: 100px;
}

.headcontentt {
    width: 360px;
    // margin: auto;
    font-size: 30px;
    font-weight: 700;
    color: #091C3F;
    font-family: 'Gilroy';

}

.subcontent {
    width: 270px;
    // margin-left: 160px;
    // font-size: 12px;
    line-height: 1.5;
    margin-top: 15px;
    color: rgba(9, 28, 63, 0.6);
}

.acceflexx {
    display: flex;
    width: 100%;
    margin: auto;
}

.imageflex {
    width: 50%;
    margin: auto;
}

.pictures {
    float: right;
    height: 200px;
    margin-right: 100px;
}
.picture{
    height: 200px;
}

.pic {
    // margin-top: 30px;
    margin-left: 100px;
}

.headcontents {
    width: 320px;
    // margin: auto;
    font-size: 30px;
    font-weight: 700;
    color: #091C3F;
    font-family: 'Gilroy';
}

.subcontentt {
    width: 270px;
    // font-size: 12px;
    line-height: 1.5;
    margin-top: 15px !important;
    color: rgba(9, 28, 63, 0.6);
}

.picc {
    // margin-left: 150px;
}
.asbl_marT_cls{
  margin-top: 60px
}

@media(max-width : 720px) {
    .accflexx {
        flex-direction: column-reverse;
        width: 100%;
    }

    .contentflex {
        width: 100%;
        margin: auto;
        padding-left: 0px;
        // text-align: center;
    }

    .imageflex {
        width: 100%;
        text-align: center;
    }

    .pic {
        margin: auto;
    }

    .picc {
        margin-left: 0px;
        margin-top: 30px;
    }

    .acceflexx {
        flex-direction: column;
        width: 100%;

    }

    .subcontentt {
        margin: auto;
        text-align: center;
    }

    .headcontentt {
        font-size: 25px;
        margin: auto;
        margin-top: 10px;
    }

    .subcontent {
        margin: auto;
        // text-align: center;
        text-align: center;
        margin-top: 15px;

    }

    .headcontents {
        font-size: 25px;
        margin: auto;
        margin-top: 10px;
        text-align: center;
    }
    .pictures {
        text-align: center;
        float: none;
        margin-right: 0px;
    }
    .picture{
        align-items: center;
    }
    .wrapp{
        width: 100%;
        text-align: center;
        float: none;
        margin-right: 0px !important;
    }
    .asbl_marT_cls{
      margin-top: 40px;
    }

}
