.containerdiv {
    display: block;
    position: relative;
    /* padding-left: 35px; */
    /* margin-bottom: 12px; */
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    }

    /* Hide the browser's default checkbox */
    .containerdiv input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    }

    /* Create a custom checkbox */
    .checkout-checkmarkk {
    position: absolute;
    top: 0;
    left: 0;
    height: 17px;
    width: 17px;
    border: 1px solid #77D08B;
    border-radius: 20px;
    background-color: rgb(18, 199, 18);
    align-items: center;
    }

    /* On mouse-over, add a grey background color */
    .containerdiv:hover input ~ .checkout-checkmarkk {
    background-color: rgb(18, 199, 18);
    }

    /* When the checkbox is checked, add a blue background */
    .containerdiv input:checked ~ .checkout-checkmarkk {
    background-color: #77D08B;
    }

    /* Create the checkout-checkmarkk/indicator (hidden when not checked) */
    .checkout-checkmarkk:after {
    content: "";
    position: absolute;
    display: none;
    }

    /* Show the checkout-checkmarkk when checked */
    .containerdiv input:checked ~ .checkout-checkmarkk:after {
    display: block;
    }

    /* Style the checkout-checkmarkk/indicator */
    .containerdiv .checkout-checkmarkk:after {
    left: 6px;
    top: 2px;
    width: 4px;
    height: 10px;
    border: solid white;
    /* border-radius: 5px; */
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    }
