.gstn-box-cls{
    width: 270px;
    height: 50px;
    background: #F5F9FF;
    border: 1px solid rgba(9, 28, 63, 0.1);
    border-radius: 6px;
}
.checkout-inputtype{
    border: none;
    outline: none;
    width: 170px;
    height:40px;
    background-color: #F5F9FF;
    color: rgba(9, 28, 63, 0.5);
    font-size: 13px;
    padding-left: 10px;
    padding-top: 7px;
}
.add-txt-btn{
    float: right;
    color: #091C3F;
    font-size: 13px;
    text-decoration: underline;
    margin-right: 20px;
    padding-top: 13px;
    cursor: pointer;
}
@media(max-width: 720px){
    .gstn-box-cls{
        width: 300px;
        height: 45px;
    }
    .checkout-inputtype{
        width: 230px;
        height:40px;
        padding-left: 10px;
        padding-top: 0px;
    }
}