.plan-purchase-div{
    width: 85%;
    margin: auto;
}
.team-flex-div{
    display: flex;
    width: 70%;
}
.emp-age-flex{
    display: flex;
    width: 100%;
    margin-top: 30px;
}
.well-txt-div{
    color: #091C3F;
    font-size: 14px;
    margin-bottom: 10px;
    padding-top: 40px;
    font-family: 'Gilroy';
}
.provider-tagg{
    font-size: 22px;
    color: #091C3F;
    text-align: center;
    margin-top: 30px;
    padding-bottom: 30px;
}
.plan-card-flxx{
    display: flex;
    justify-Content: space-between;
    margin-top: 30px;
}
.emp-count-skele{
    width: 30%;
    margin: auto;
}
.age-price-skele{
    width: 80%;
    margin-left: 15px;
}
// .webview-cls{
//     display: block;
// }
.mobview-clss{
    display: none;
}

.mobileShow{
    display: none;
}

.total_member_mobile{
    font-weight: 600;
    font-family: gilroy;
    width: 90%;
    margin: auto;
    padding-top: 30px;
    padding-bottom: 10px;
}

.mobilePlan{
    display: none;
}

@media screen and (min-width: 1500px){
    .plan-purchase-div{
        width: 1200px;
    }
    .mobview-cls{
        display: none !important;
    }
  }

@media(max-width:800px) and (min-width:700px){

    .team-flex-div{
        flex-direction: row !important;
    }

}

@media(max-width: 800px){

    .mobileShow{
        display: block;
        height: 121px;
        margin-top: 20px;
        background-color: #fff;
    }

    .sticky_paln_nav{
        position: -webkit-sticky; /* Safari */
        position: sticky;
        top: 40px;
        z-index: 9999;
    }



    .plan-purchase-div{
        width: 100%;
        margin: auto;
    }



    .main_nav_div{
        overflow-x: scroll;
    }

    /* Hide scrollbar for Chrome, Safari and Opera */
    .main_nav_div::-webkit-scrollbar {
        display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    .main_nav_div {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
    }

    .blockmain_div{
        display: inline-flex;
    }

    .price_block_mobile{
        width: 110px;
        text-align: center;
        color: gray;
        // padding: 5px 20px 5px 20px;
    }

    .price_block_mobile_scroller{
        margin-top: 16px;
        background-color: #1987FB;
        height: 3px;
        width: 110px;
        position: relative;
        transition: all .3s;
        transition-timing-function: cubic-bezier(0.63, -0.41, 0.31, 1.49);
        transition-delay: 0s;
    }



    .team_and_member{
        width: 90%;
        margin: auto;
        margin-left: 20px;
    }

    .emp-count-skele{
        display: none;
        width: 100%;
    }

    .age-price-skele{
        width: 100%;
        margin: auto;
    }


    .team-flex-div{
        flex-direction: column;
        width: 100%;
    }
    .well-txt-div{
        text-align: left;
    }
    .plan-card-flxx{
        display: none;
        flex-direction: column;
        width: 100%;
        margin-top: 20px;
        padding-top: 20px;
    }

    .mobilePlan{
        display: block;
        overflow-x: auto;
    }

    .class_plan_flex_mob{
        display: inline-flex;
        flex-direction: row;
        // width: 100%;
        margin-top: 20px;
        padding: 20px 20px 0px 30px;
    }

    .emp-age-flex{
        flex-direction: column;
        width: 100%;
        margin-top: 40px;
    }

    // .webview-cls{
    //     display: none;
    // }
    .mobview-clss{
        display: block;
    }
    .currPlanSelCls{
      color: #1987FB;
    }
}
