.main-flx{
    display: flex;
    width:90%;
    margin: auto;
    padding-top: 25px;
    padding-bottom: 70px;
}
.checkout-skel{

    border: 1px solid rgba(9, 28, 63, 0.05);
}
.summary-skele{
    display: block;
    width: 35%;
    margin-left: 20px;
}
.frst-flx{
    width: 60%;
    margin: auto;
}
.web-view-compo{
    display: block;
}
.mob-view-compo{
    display: none;
}
.mobview-float-cls{
    display: none ;
}

@media screen and (min-width: 1500px){
    .main-flx{
        width: 1200px;
    }
  }
  @media(max-width: 800px){

         .summary-skele{
        display: none;
    }
    .frst-flx{
        width: 100%;
        margin: auto;
        text-align: center;
    }
    .web-view-compo{
        display: none;
    }
    .mob-view-compo{
        display: block;
    }
    .main-flx{
        width: 100%;
        margin: auto;
        padding-top: 20px;
        padding-bottom: 40px;
    }
    .mobview-float-cls_old{
        position: fixed;
        width: 100%;
        bottom: 0px;
        top: 85vh;
        // height: 190px;
        padding: 30px 0px 10px 0px;
        background-color: white;
        display: block;
        text-align: center;
    }
    .mobview-float-cls{
      position: fixed;
      width: 100%;
      bottom: 0px;
      background-color: white;
      display: block;
      height: 100px;
      padding-top: 20px;
      text-align: center;
    }
    .subscribe-btnn{
        font-family: Roboto;
        border: none;
        outline: none !important;
        width: 270px;
        height: 56px;
        background: #007DFF;
        border-radius: 40px;
        color: white;
        font-size: 18px;
        font-weight: 500;
    }
  }
