


.ContentCovid{
    width: 850px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
}
.mainhaeading-txt{
    font-family: Gilroy;
    color: #16356D;
    font-size: 24px;
    font-weight: 800;
    padding-top: 47px;
    text-align: center;

}
.card-subheader{
    font-family: Roboto;
    font-size: 15px;
    line-height: 18px;
    color: rgba(9, 28, 63, 0.5);
    margin-bottom: 50px;
    text-align: center;
}

@media(max-width: 720px){
    .ContentCovid{
        width: fit-content;
        display: flex;
        flex-wrap: nowrap;
        // overflow-x: auto;
        margin: auto;
    }

    .Clardscrollmaoboor{
        overflow-x: auto;
    }
    .mainhaeading-txt{
        text-align: left;
        line-height: 1.2;
    
    }
    .card-subheader{
        text-align: left;
    }
    .cov-wrpdiv{
        margin-left: 18px;
    }
}