@media(max-width:720px){
    .MobileRange{
        display: block;
    }

    .years-cls{
        margin: 15px 5px 0px 5px;
    }

    .WebRange{
        display: none;
    }

    .ranges_mobile{
        display: flex;
        flex-wrap: wrap;
    }

    .price_range_mob{
        font-weight: 600;
        text-align: center;
        margin: 10px 0px 0px 0px;
    }

    .monthly-prc-cls{
        color: rgba(9, 28, 63, 0.8);
        font-size: 14px;
        margin: auto;
        width: 170px;
        text-align: center;
    }
}
