.markrtPgCls .btnflexx{
    width: 40%;
    margin-top: auto;
    margin-bottom: auto;
    padding-left: 6%;
}
.markrtPgCls .startedbtn{
    border: none;
    outline: none;
    width: 150px;
    padding: 10px 14px 10px 14px;
    border-radius: 40px;
    background-color: #3c8bf3;
    color: white;
    cursor: pointer;
    font-size: 15px;
}
.markrtPgCls .demobtn{
    border: none;
    outline: none;
    width: 150px;
    padding: 10px 14px 10px 14px;
    border-radius: 40px;
    background-color: white;
    color: #091C3F;
    margin-left: 30px;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
}
.markrtPgCls .mobtext{
    display: none;
}

.markrtPgCls .infodiv{
    width: 570px;
    margin: auto;
    margin-right: 60px;
    text-align: left;
}

.markrtPgCls .mainHeading_max{
  font-size: 30px;
    line-height: 40px;
    font-weight: 700;
    width: 100%;
    margin-left: 0%;
   font-family: 'Gilroy';
    color: #091C3F;
}
@media(max-width: 720px){


    .markrtPgCls .demoflexx{
        padding: 0px;
    }
    .markrtPgCls .demoflexx{
        flex-direction: column;
    }
    .markrtPgCls .infodiv{
        width: 100%;
        text-align: center;
    }
    .markrtPgCls .btnflexx{
        width: 80%;
        margin: auto;
        text-align: center;
        margin-top: 10px;
    }
    .markrtPgCls .markrtPgCls .demodiv{
        padding-top: 20px;
        margin-bottom: 30px;
    }
    .markrtPgCls .markrtPgCls .demobtn{
        display: block;
      padding: 10px 0px;
      margin:auto;
      margin-top: 15px;
        /*display: none;*/
    }
    .markrtPgCls .mobtext{
        display: block;
        margin-bottom: 20px;
        margin-top: 0px;
    }
    .markrtPgCls .mainHeading_max{
      font-size: 25px;
line-height: 124%;
font-weight: 700;
margin-top: 10px;
  margin-left: 0%;
    width: 100%;
    padding-left: 10%;
        padding-right: 10%;
        letter-spacing: 0.005em;
    }
    .markrtPgCls .btnflexx{
      display: block;
      padding: 5px 5px 10px;
      margin-bottom: 10px;
    }
    .markrtPgCls .startedbtn{
      padding: 10px;
    }
    // .req_btns_cls{
