.markrtPgCls .com-section{
    display: flex;
    margin-top: 30px;
}

.markrtPgCls .name-input::placeholder {
    color: rgba(9, 28, 63, 0.5)!important;
    opacity: 1; /* Firefox */
  }

  .markrtPgCls .name-input:-ms-input-placeholder { /* Internet Explorer 10-11 */
   color: rgba(9, 28, 63, 0.5)!important;
  }

  .markrtPgCls .name-input::-ms-input-placeholder { /* Microsoft Edge */
   color: rgba(9, 28, 63, 0.5)!important;
  }

.markrtPgCls .erri{
    color:red;
    padding-top: 4px;
}
.markrtPgCls .img-section{
    width: 50%;
    text-align: center;
    // float: right;
    margin-top: 70px;
}
.markrtPgCls .addtoRight{
    float: right;
    margin-right: 80px;
}
.markrtPgCls .grp-img{
    height: 250px;
    text-align: center;
}
.markrtPgCls .header-div{
    width: 350px;
    font-family: Gilroy;
    margin: auto;
    text-align: center;
    color: #091C3F;
    font-size: 24px;
    font-weight: 800;
}
.markrtPgCls .subhead-divv{
    width: 350px;
    margin: auto;
    text-align: center;
    color: #091C3F;
    font-size: 18px;
    margin-top: 10px;
}
.markrtPgCls .input-section{
    width: 50%;
    padding-left: 80px;
}
.markrtPgCls .qoute-div{
    width: 380px;
    // height: 500px;
    padding: 10px 0px 20px 0px;
    background: #FFFFFF;
    box-shadow: 0px 10px 30px rgba(94, 125, 182, 0.1);
    border-radius: 5px;
}
.markrtPgCls .quote-txt{
    color: #091C3F;
    font-size: 22px;
    font-family: Gilroy;
    font-weight: 800;
    text-align: center;
    margin-top: 20px;

}
.markrtPgCls .outer-divv{
    width: 315px;
    height: 45px;
    background: #F5F9FF;
    border: 1px solid rgba(9, 28, 63, 0.1);
    border-radius: 6px;
}
.markrtPgCls .wrp-clss{
    width: 320px;
    margin: auto;
    margin-top: 30px;
}
.markrtPgCls .label-name{
    color: #091C3F;
    font-size: 14px;
    margin-bottom: 3px;
}
.markrtPgCls .name-input{
    border: none;
    outline: none;
    width: 280px;
    height: 40px;
    background-color: #F5F9FF;
    padding-left: 20px;
}
.markrtPgCls .submit-btn{
    border: none;
    outline: none;
    width: 190px;
    height: 45px;
    background: #1987FB;
    border-radius: 40px;
    color: white;
}
@media(max-width: 720px){
    .markrtPgCls .com-section{
        flex-direction: column;
        margin-top: 30px;
    }

    .markrtPgCls .addtoRight{
        float: none;
        margin-right: 0px;
    }

    .markrtPgCls .img-section{
        width: 100%;
        margin: auto;
        margin-top: 30px;
    }
    .markrtPgCls .input-section{
        width: 100%;
        padding-left: 0px;
    }
    .markrtPgCls .grp-img{
        height: 200px;
        margin-top: 20px;
        text-align: center;
    }
    .markrtPgCls .qoute-div{
        width: 100%;
        margin: auto;
    }
}
