.pln-price-cls-web {
    background-color: white;
    border-radius: 3px;
    // margin: auto;
    margin-top: 20px;
    padding-bottom: 40px;
    border: 1px solid rgba(9, 28, 63, 0.05);
}

.plan-prc-team {
    font-family: "Gilroy";
    color: #091C3F;
    font-size: 22px;
    font-weight: 800;
    padding-top: 20px;
    margin-left: 50px;
}

.hr-sec {
    width: 90%;
    margin: auto;
}

.edit-box-cls {
    width: 50px;
    height: 20px;
}

.pln-price-cls-mob {
    display: none;
}
.age-amount-flx .head-clssdiv{
  font-weight: bold;
}
.age-amount-flx .head-team-only{
  font-weight: bold;
}
.age-amount-flx .head-team-fam{
  font-weight: bold;
}
@media(max-width:720px) {
    .head-clss {
        // width: fit-content;
        // margin-top: 20px;
        color: #091C3F;
        font-size: 14px;
        margin-bottom: 5px;
    }
    .age-amount-flx {
        display: flex;
        width: 100%;
        justify-content: space-evenly;
    }

    .plan-prc-team {
        text-align: left;
        padding-top: 20px;
        margin-left: 30px;
    }


    .pln-price-cls-web {
        // display: none;
        margin-bottom: 15px;

    }

    .pln-price-cls-mob {
        display: block;
        background-color: white;
        border-radius: 3px;
        margin-bottom: 80px;
        margin-top: 20px;
        padding-bottom: 40px;
    }
}
