//checkmark
  .SideCheck{
    position:relative;
    height:25px;
    width:25px;
    float:right;
    background: linear-gradient(to right top, #fff 49%, #1987FB 50%);
    // background:-webkit-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(209,209,209,1) 50%, rgba(226,226,226,1) 50%, #1987FB 50%, #1987FB 100%)
  }
  
  .checkmark_bss {
      display:inline-block;
      width: 22px;
      height:22px;
      -ms-transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
  }
  
  .checkmark_stem {
      position: relative;
      width:2px;
      height:10px;
      background-color:#fff;
      left:12px;
      top:-2px;
      border-radius:5px 5px 0px 0px
  }
  
  .checkmark_kick {
      position: relative;
      width:4px;
      height:3px;
      background-color:#fff;
      left:9px;
      top:-5px;
      border-radius:5px 0px 0px 5px
  }
