.BottomSheet{
    position: fixed;
    bottom: -100px;
    margin: 0;
    width: 100%;
    background-image: linear-gradient(to right,#007DFF,#71b5fd);
    z-index: 19;
    box-shadow: 0 2px 3px rgb(0 0 0 / 20%);
    transition: ease-in-out .5s;

}

.wrapbanner{
    display: flex;
    width: fit-content;
    margin: auto;
}

.BannerText{
    height: 65px;
    color: #fff;
    font-size: 18px;
    margin-right: 10px;
    padding-top: 20px;
}

.BottomSheetShow{
    bottom: 0px;
}

.btnGetstatredBanner{
    animation: color-blink 1s infinite;
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
    padding: 10px 30px;
    border: 0;
    border-radius: 20px;
    transition: all .2s;
}

.closeBanneridc{
    position: absolute;
    right: 0px;
    padding-right: 15px;
    padding-top: 20px;
}

.closeBanner{
    height: 20px;  
  }


@keyframes color-blink{
    0%, 49% {
        background-color: #fddd6c;
    }
    
    50%, 100% {
        background-color: #fff;
    }
}


@media(max-width:720px){
    .BannerText{
        height: auto;
        font-size: 13px;
        margin-right: 0px;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 10px;
    }

    .btnGetstatredBanner{
        padding: 10px 0px;
        width: 100px;
        margin-right: 45px;
        font-size: 11px;
    }

    .closeBanner{
      height: 14px;  
    }
}