.inputtypee{
    outline: none;
    width: 100%;
    border: 1px solid rgba(9, 28, 63, 0.1);
    background-color: #F5F9FF;
    color: #091C3F;
    border-radius: 6px;
    font-size: 13px;
    padding: 10px;
}

.DisableChanges{
    opacity: 0.3;
    cursor: not-allowed;
}



.inputtypee:hover{
    border: 1px solid rgba(9, 28, 63, 0.3);
}

.inputtypee:focus{
    border:1px solid #1987FB;
}
 
.errorInput{
    border:1px solid #FD8686;
}

.inputtypee:focus{
    border:1px solid #1987FB;
}

.error_input_text{
    color:#FD8686;
    font-size:12px;
    text-align: left;
    margin: auto;
    // padding-left: 5px;
    padding-top: 2px;
}

.inputtypee:disabled{
 opacity: 0.7;
 cursor: not-allowed;
}

