.qutn-div{
    display: inline-block;
    width: 12px;
    height: 12px;
      border-radius: 50%;
    border:  1px solid rgba(0, 0, 0, 0.1);
    background-color: #F5F9FF;
    font-size: 8px;
    color: #1987FB;
    text-align: center !important;
    margin-left: 5px;
    margin-right: 3px;
    padding-bottom: 10px;
}
input.visited{
    outline: 1px solid #1987FB;
    border-radius: 6px;
}
input:focus{
    outline: 1px solid #1987FB;

}

.LableFloat{
