.how-it-work{
    // width: 600px;
    background-color: white;
    border-radius: 3px;
    margin-top: 20px;
    padding-bottom: 40px;
    border: 1px solid rgba(9, 28, 63, 0.05);
}
.pay-text{
    color: #091C3F;
    font-size: 22px;
    font-weight: 800;
    margin-left: 40px;
    padding-top: 40px;
    font-family: "Gilroy";
    // padding-bottom: 8px;
}
.how-it-workkk{
    color: rgba(9, 28, 63, 0.5);
    font-size: 14px;
    // margin-left: 40px;
    padding-top: 20px;
    margin-right: 80px;
}
.howit-work-mob{
    display: none;
}

@media(max-width: 720px){
    .how-it-work{
        display: none;
    }
    .howit-work-mob{
        display: block;
        width: 100%;
        height: 92px;
        margin: auto;
        background-color: white;
        border-radius: 3px;
        margin-top: 20px;
        padding: 20px 20px 20px 30px;
    }
    .pay-texttt{
        width: 50%;
        color: #091C3F;
        font-size: 16px;
        font-weight: bold;
        text-align: left;
    }
    .read-more-cls{
        font-family: Gilroy;
        float: right;
        padding-top: 10px;
        text-decoration: underline;
        color: #1987FB;
    }
    .pay-text{
        margin-left: 0px;
        text-align: center;
    }
    .how-it-works-cls{
        color: rgba(9, 28, 63, 0.5);
        font-size: 14px;
        margin-left: 0px;
        padding-top: 20px;
        margin-right: 20px;
        text-align: left;
    }

}
