.container {
    display: block;
    position: relative;
    /* padding-left: 35px; */
    /* margin-bottom: 12px; */
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    }

    /* Hide the browser's default checkbox */
    .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    }

    /* Create a custom checkbox */
    .planpage-checkmarkk {
    position: absolute;
    top: 0;
    left: 0;
    height: 16px;
    width: 17px;
    // border: 1px solid #dddee2;
    border-radius: 20px;
    background-color: #77D08B;;
    align-items: center;
    }

    /* On mouse-over, add a grey background color */
    .container:hover input ~ .planpage-checkmarkk {
    background-color: #77D08B;
    }

    /* When the checkbox is checked, add a blue background */
    .container input:checked ~ .planpage-checkmarkk {
    background-color: #77D08B;
    }

    /* Create the planpage-checkmarkk/indicator (hidden when not checked) */
    .planpage-checkmarkk:after {
    content: "";
    position: absolute;
    display: none;
    }

    /* Show the planpage-checkmarkk when checked */
    .container input:checked ~ .planpage-checkmarkk:after {
    display: block;
    }

    /* Style the planpage-checkmarkk/indicator */
    .container .planpage-checkmarkk:after {
    left: 6px;
    top: 2px;
    width: 5px;
    height: 10px;
    border: solid white;
    /* border-radius: 5px; */
    border-width: 0 2px 2px 0;
    border-radius: 2px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    }
