.inputtdivw{
    border: 1px solid rgba(9, 28, 63, 0.1);;
    margin-top: 32px;
    width: 240px;
    margin: auto;
    background-color: #F5F9FF;
    border-radius: 6px;
}
.outerdiv{
    display: flex;

}
.digitflex{
    width: 40px;
}
.numberc{
    color: rgba(9, 28, 63, 0.8);
    font-size: 13px;
    text-align: center;
    padding: 12px;
}
.inputStyle{
    width: 99% !important;
    border: none;
    outline: none !important;
    font-size: 13px;
    background-color: #F5F9FF;
    color: #091C3F;
    padding: 10px;
    margin: 2px;
}



input:focus{
    outline: none;
}
input.visited{
    outline: none;
}

.inputtdivw:hover{
    border: 1px solid rgba(9, 28, 63, 0.3);
}

.inputtdivw:active{
    border-color: #1987FB;
}

.inputflex{
    width: 100%;
}
.btnDisbChk:disabled {
    background-color: darkgray;
}
