
.purchaseBackground{
    background-color:  #F5F9FF;
    height: 90vh;
}


.relSkelpjone{
    position: relative;
    height: 100%;
}

.purchaseFlex{
    position: absolute;
    top: 0; bottom: 0px; left: 0; right: 0;
    margin: auto;
    height: fit-content;
}

.mpinNote{
    background: #FFF6E3;
    border: 1px solid #FFEBC1;
    box-sizing: border-box;
    border-radius: 4px;
    text-align: left;
    padding: 15px 20px;
    margin-top: 25px;
    font-size: 13px;
}

.flex-div-pu{
display: flex;
}

.FlexValuePurchase1{
    text-align: center;
    width: 53%;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.05);
    border-right: #fff;
    box-sizing: border-box;
    box-shadow: 0px 30px 50px rgba(190, 213, 255, 0.14);
    border-radius: 3px 0px 0px 3px;
}

.FlexValuePurchase2{
    width: 47%;
    background: linear-gradient(112.79deg, #0780FF 7.49%, #0964C2 102.04%);
    // border-radius: 0px 3px 3px 0px;

    background-image: url(https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1596475612/New-home-screen/signUpSlidimg_b0ptoh.svg);
    background-size: cover;
    background-repeat: no-repeat;
}

.HeadingPurchase{
    font-family: Gilroy;
    font-size: 20px;
    font-weight: 700;
    color:#091C3F;
    padding: 0px 10px 0px 10px;
}

.PurchaseMainContent{
    position: relative;
    top: 14vh;
}
.phoneNumContent{
  top: 2vh;
}
.steps-prchase-d{
    text-align: center;
    color: rgba(9, 28, 63, 0.5);
    font-size: 12px;
    margin-top: 20px;
}

// div{
// border: 1px solid red;
// }


.PurchaseMainSection{
    border: 1px solid #fff;
    height: 80%;
}


.PurchaseButtonSection{
    border: 1px solid #fff;
    // padding-top: 20px;
    height: 20%;
}


.chck-updates {
    font-size: 13px;
    color: #091C3F;
    text-align: center;
}

.chckbx-content {
    font-size: 12px;
    color: rgba(9, 28, 63, 0.5);
    text-align: center;
}
.sent-to-num{
    font-size: 12px;
    color: #091C3F;
}
.edit-otp-screen{
    font-size: 12px;
    color: #091C3F;
    // text-decoration: underline;
    margin-left: 10px;
    margin-bottom: 5px;
    cursor: pointer;
    border-bottom: .1px solid rgb(24, 24, 24);
}
.resend-clck{
    color: #1987FB;
    font-size: 13px;
    margin-top: 5px;
    cursor: pointer;
}

.signUpNewTxtCls{
  padding-top: 15px;
  width: 290px;
  margin: auto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.005em;
  color: rgba(9, 28, 63, 0.5);
}
@media(max-width:1023px){


.purchaseBackground{
    height: fit-content;
}
    .purchaseFlex{
        padding-top: 30px;
        position: unset;
        top: unset;
        left: unset;
        -ms-transform: unset;
        transform:unset;
    }

    .flex-div-pu{
        display: flex;
        flex-direction: column;
        }

    .FlexValuePurchase1{
        border: 1px solid rgba(0, 0, 0, 0.05);
        width: 100%;
        height: 70%;
    }
    .FlexValuePurchase2{
        width: 100%;
        height: 50%;
    }

    .PurchaseMainContent{
        top: 30%;
    }
    .phoneNumContent{
      top: 5%;
    }
    .PurchaseButtonSection{
        height: 20%;
        padding-top: 0px;
    }

    .PurchaseMainSection{
        height: 80%;
    }
}
