.promocode-apply{
    width: 90%;
    margin: auto;
    height: 50px;
    border: 1px solid rgba(9, 28, 63, 0.1);
    background-color: #F5F9FF;
    border-radius: 6px;
    margin-top: 15px;
}
.promocode-apply:hover{
    border: 1px solid #1987FB;
}
.code-cls{
    border: none;
    outline: none;
    width: 200px;
    color: rgba(9, 28, 63, 0.5);
    font-size: 14px;
    padding-top: 13px;
    margin-left: 18px;
    background-color: #F5F9FF;
}
.apply-code-div{
    float: right;
    color: #091C3F;
    font-size: 14px;
    text-decoration: underline;
    margin-top: 12px;
    margin-right: 30px;
    cursor: pointer;
}