
.pay-total-mob{
    display: none;
}

@media(max-width: 720px){
    .pay-total-mob{
        display: block;
        width: 100%;
        height: 80px;
        background-color: white;
        margin-bottom: 20px;
        padding: 20px 20px 20px 20px;
    }
    .view-details-div{
        text-decoration: underline;
        float: right;
        margin-right: 10px;
        padding-top: 5px;
        cursor: pointer;
        color: #1987FB;
    }
    .view-details-div:hover{
        color: #1987FB;
    }
    .total-txtdiv{
        color: rgba(9, 28, 63, 0.8);
        font-size: 14px;
        font-family: roboto;
    }
    .total-rupeediv{
        color: #091C3F;
        font-size: 18px;
    }
}
