.summary-cls{
    position: fixed;
    width: 400px;
    // height: 620px;
    background-color: white;
    border-radius: 3px;
    padding-bottom: 40px;
    border: 1px solid rgba(9, 28, 63, 0.05);
}
.summary-div-cls{
    color: #091C3F;
    font-size: 22px;
    font-weight: 800;
    padding-top: 22px;
    margin-left: 30px;
    font-family: "Gilroy";
}
.hr-sec-div{
    width: 90%;
    margin: auto;
}
.summ-flx{
    display: flex;
    width: 100%;
}
.first-flx{
    width: 70%;
    margin: auto;
}
.second-flx{
    width: 30%;
    margin: auto;
}
.monthly-pln-price{
    color: rgba(9, 28, 63, 0.8);
    font-size: 16px;
    padding-left: 24px;
    margin-top: 6px;
}
.cmn-sum-cls{
    color: rgba(9, 28, 63, 0.5);
    font-size: 16px;
    padding-left: 24px;
    padding-top: 8px;
    margin-top: 6px;
}
.cmn-sum-clss{
    color: rgba(9, 28, 63, 0.5);
    font-size: 16px;
    padding-left: 23px;
    margin-top: 6px;
}
.cmn-sum-div{
    color: rgba(9, 28, 63, 0.8);
    font-size: 16px;
    padding-left: 24px;
    font-weight: bold;

}

.subscribe-btn{
    border: none;
    outline: none !important;
    width: 270px;
    height: 50px;
    background: #007DFF;
    border-radius: 40px;
    color: white;
    font-size: 16px;
    font-weight: 500;
}
.subscribe-btn:hover{
    background-color: #1B6FC9;
}

@media(max-width: 720px){
    .summary-cls{
        // position: fixed;
        width: 100%;
        // height: 300px;
        background-color: white;
        border-radius: 3px;
        padding-bottom: 40px;
    }
    .summary-btn_old{
        position: fixed;
        top: 80vh;
        width: 100%;
        padding: 20px 0px 20px 0px;
        background-color: white;
    }
    .summary-btn{
    //   position: fixed;
    //   width: 100%;
    //   bottom: 0px;
    //   background-color: white;
      padding-top: 40px;
    //   height: 100px;
    }
    .summary-btn div {
      margin-top: 0px !important;
    }
}
