
      $SliderHeight:200px;

      /* Modal Content */
      .info-modal-content {
        background-color: #fefefe;
        position: fixed;
        width: 100%;
        bottom: 0%;
        margin: auto;
        padding: 20px;
        animation: aanimationDropCenter .3s ;
        max-height: 550px;
        overflow-y: auto;
      }
      @keyframes aanimationDropCenter {
        0% {
            bottom:-$SliderHeight;
        }
    }

      /* The Close Button */
      .info-close {
        position: fixed;
        text-align: right;
        height: 32px;
      }

      .info-close:hover,
      .info-close:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
      }


    .info-main-con{
        width: 60%;
        margin: auto;
    }

  @media(max-width: 720px){
    .info-main-con{
        width: 90%;
        margin: auto;
    }
  }

  @media(max-width: 380px){
    .info-modal-content {
      max-height: 470px;
    }
  }