.mem-only-cls{
    display: flex;
    width: 360px;
    height: 90px;
    border: 1px solid rgba(9, 28, 63, 0.5);
    background-color: white;
    border-radius: 3px;
    // width: 250px;
    margin-right: 20px;
    padding: 20px 10px 0px 10px;
    cursor: pointer;
}
.prps-dta-head{
    font-size: 14px;
    font-weight: 600;
    color: #091C3F;
    margin-left: 10px;
    font-family:gilroy
}
.prps-dta-sub{
    font-size: 12px;
    color: rgba(9, 28, 63, 0.5);
    margin-left: 10px;
}
.radio-btnn{
    margin-top: 10px;
}

.curntSelectionPlanRadio{
    border: 1px solid #1987FB;
}
.curntSelectionPlanRadio span{
    border: 1px solid #1987FB;
}

@media(max-width: 720px){
    .mem-only-cls{
        width: 100%;
        height: auto;
        padding-bottom: 20px;
        margin: auto;
        margin-right: 0px;
        margin-top: 10px;
        // padding: 20px 0px 20px 0px;
    }
    .prps-dta-head{
        margin-left: 0px;
    }
    .wrp-contentdiv{
        width: 90%;
        // margin: auto;
    }
    .radio-btnn{
        width: 10%;
        margin-top: 15px;
        margin-left: 10px;
    }
    .prps-dta-sub{
        margin-left: 0px;
        margin-top: 2px;
    }
}
