.MainSec{
    padding: 40px 0px 40px 0px;
}

.SectionHead{
    font-weight: 700;
    color:#091C3F;
    text-align: center;
    font-size: 30px;
    font-family: 'Gilroy';
}

// div{

//     border: 1px solid red;
// }

.CardBlock{
    height: 295px;
    width: 230px;
    background-color: #fff;
    border: 1px solid rgba(9, 28, 63, 0.05);
    box-sizing: border-box;
    border-radius: 3px;
}

.Card2Block{
    width: 150px;
}

.flexContent{
    display: flex;
    width: 1000px;
    margin:auto;
}

.flexContent2{
    display: flex;
    width: 1000px;
    margin:auto;
}


@media(max-width:720px){

    .SectionHead{
        font-size: 25px;
        padding: 0px 30px 0px 30px;
    }

    .flexContent{
        overflow-x: scroll;
        padding-left: 10%;
        width: auto;
    }

    .Card2Block{
        width: auto;
        padding: 0px 20px 0px 20px;
    }

    .flexContent2{
        display: flex;
        flex-wrap: wrap;
        width: auto;
    }

    .flexContent2>div {
        flex: 0 50%;
      }

    .flexContent::-webkit-scrollbar {
        display: none;
    }
}
