$heading: 30px;
$subheading: 12px;

.graydiv{
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 30px;
   
}
.heading{
    width: fit-content;
    margin: auto;
    color:#091C3F;
    font-size: $heading;
    font-weight: 700;
}
.inputtypees{
    // border: none;
    outline: none !important;
    border: 1px solid rgb(224, 221, 221);
    width: 230px;
    padding: 13px;
    font-size: 14px;
    padding-left: 25px;
    color: rgba(9, 28, 63, 0.5);
    border-radius: 33px;
    background-color: #F5F9FF;
}
.companyinput{
    outline: none !important;
    border: 1px solid rgb(224, 221, 221);
    width: 230px;
    padding: 13px;
    font-size: 14px;
    padding-left: 30px;
    color: rgba(9, 28, 63, 0.5);
    border-radius: 33px;
    background-color: #F5F9FF;

}
.gstinbtn{
    border: none;
    outline: none !important;
    width: 160px;
    padding: 13px;
    background-color: #3c8bf3;
    color:white;
    font-size: 14px;
    border-radius: 33px;
    // margin-left: -70px
    cursor: pointer;
}
.OR{
    font-size: 25px;
    color: #000000;
    padding: 0px 30px 0px 30px;
}
.btndiv{
    display: flex;
    margin: auto;
    padding: 20px;
    justify-content: center;
    // margin-top: 30px;
}
.textdiv{
    width: fit-content;
    margin: auto;
    padding: 20px;
}
.subdiv{
    width: 250px;
    // margin: auto;
}
.gst{
    padding-right: 30px;
}


    @media(max-width: 720px){
        .flexdiv{
             flex-direction: column;
             text-align: center;
            }
         .btndiv{
            flex-direction: column;
        }
        .OR{
            text-align: center;
            padding: 0px 0px 0px 0px;
            margin-top: 20px;

        }
        .gst{
            padding-right:0px !important;
            margin-top: 20px;
          }
          .gstinbtn{
              width: 180px;
              margin-top: 40px;
        }
        .inputtypees{
            width: 250px;
            padding-left: 30px;
        }
        .companyinput{
          width: 250px;
          padding-left: 30px;
        
        }
    }

