.faqHeading{
    font-family: Gilroy;
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    text-align: center;
    margin-top: 30px;
}
.faqSubHeading{
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    letter-spacing: -0.0045em;
    color: rgba(9, 28, 63, 0.6);
    width: 400px;
    margin:auto;
}



.main_nav_divf{
    overflow-x: scroll;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.main_nav_divf::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.main_nav_divf {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.blockmain_divf{
    display: flex;
    width:fit-content;
    margin:auto;
}

.price_block_mobilef{
    // width: 110px;
    cursor: pointer;
    text-align: center;
    color: gray;
    padding: 5px 30px 0px 30px;
}

.selevctedfaq{
    background-color: #1987FB;
    height: 3px;
    width: 100%;
    margin-top: 10px;
}

.header_color_faq{
    color:#1987FB
}

.hrLineFaq{
    border: .3px solid rgba(9, 28, 63, 0.3);
    width: 850px;
    margin:auto;
}

.faqSecQstn{
    width: 800px;
    margin: auto;
}

.divquestions{

}

.qstfaq{
    font-family: Gilroy;
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    line-height: 150.6%;
    /* or 27px */

    letter-spacing: 0.0045em;

    color: #091C3F;
    width: 100%;
}

.expandicofaq{
    padding-right: 10px;
}
.ansfaq{
    overflow: hidden;
    margin-right: 50px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150.6%;
    /* or 21px */

    letter-spacing: 0.0045em;

    color: rgba(9, 28, 63, 0.6);
}

.infaqi{
    transition: all 2s ease-in-out;
    -webkit-transition: all 2s ease-in-out; /** Chrome & Safari **/
    -moz-transition: all 2s ease-in-out; /** Firefox **/
    -o-transition: all 2s ease-in-out; /** Opera **/
}

.SearchSecFaq{
    margin:40px 0px 10px 0px;
}

@media(max-width:800px){

    .faqSecQstn{
        width: 90%;
    }

    .blockmain_divf{
        overflow-x: auto;
        width:100%;
    }

    .blockmain_divf::-webkit-scrollbar {
        display: none;
      }
      
      /* Hide scrollbar for IE, Edge and Firefox */
      .blockmain_divf {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
      }

    .price_block_mobilef{
        width: max-content;
    }

    .hrLineFaq{
        width: 100%;
    }
    
}