
.notfound-flx{
    display: flex;
    width: 60%;
    margin: auto;
    margin-top: 100px;
}
.contents-flx{
    width: 42%;
    margin: auto;
}
.oopsimg-flx{
    width: 58%;
    margin: auto;
}
.oops-txt{
    font-family: Gilroy;
    font-weight: bold;
    font-size: 78px;
    line-height: 24px;
    color: #1987FB;
    padding-bottom: 25px;
}
.notpage-txtxs{
    font-family: Roboto;
    font-size: 14px;
    line-height: 24px;
    color: #091C3F;
    padding-top: 15px;
}
.links-div{
    font-family: Roboto;
    font-size: 14px;
    line-height: 24px;
    color: #091C3F;
    padding-top: 7px;
}
.links-div:hover{
    color: #1987FB;
}
.imagee{
    width: 380px;
    padding-top: 40px;
}

@media(max-width: 800px){
    .notfound-flx{
        flex-direction: column-reverse;
        width: 100%;
        margin: auto;
        margin-top: 100px;
    }
    .contents-flx{
        width: 100%;
        margin: auto;
    }
    .oopsimg-flx{
        width: 100%;
        margin: auto;
        text-align: center;
    }
    .oops-txt{
        text-align: center;
        padding-top: 50px;
    }
    .notpage-txtxs{
        text-align: center;
    }
     .links-div{
        text-align: center;
    }
    .imagee{
        width: 340px;
    }
}
