.inputtdiv{
    border: 1px solid rgba(88, 109, 148, 0.1);
    width: 350px;
    margin: auto;
    background-color: #F5F9FF;
    border-radius: 6px;
}
.inputtdiv:focus {
    border: 1px solid #1B6FC9;
}

.inputtdiv:active {
    border: 1px solid #1B6FC9;
}

.outerdiv{
    display: flex;

}
.digitflex{
    width: 40px;
}
.number{
    color: rgba(9, 28, 63, 0.8);
    font-size: 13px;
    text-align: center;
    padding: 10px;
}
.inputStyle{
    width: 100%;
    border: none;
    outline: none !important;
    font-size: 13px;
    background-color: #F5F9FF;
    color: #091C3F;
    padding: 10px;
    border-radius: 6px;

}
input:focus{
    outline: none;
}
input.visited{
    outline: none;
}

.inputflex{
    width: 100%;
}
.btnDisbChk:disabled {
    background-color: darkgray;
}
