.CovidSkliton{
    display: flex;
    background-color: #F5F7FA;
    width: 100%;
    position: absolute;
    top: 55px;
    bottom: 0;
}

.CoverageItem1{
    width: 100%;
}

.CoverageItem2{
    display: block;
    width: 450px;
    background-color: #fff;
}
.CoverageItem-Mob{
    display: none;
}

@media(max-width: 800px){
    .CovidSkliton{
        flex-direction: column;
        bottom: auto;
    }
    .CoverageItem2{
        display: none;
    }
    .CoverageItem-Mob{
        display: block;
    }

    .CovrageMon{
        border: 1px solid #fff;
        background: #fff;
        margin-top: 3px;
    }
     
}