.ordive{
    color: #091C3F;
    font-size: 12px;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 20px;
}


.GSTINContent{
    position: relative;
    top: 7vh;
}

.GSTINSection{
    height: 25%;
}

.searchCmpNamGst input:focus{
  background: #f5f9ff url("https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1592632155/New-home-screen/searchImg_rvdmmh.svg") no-repeat right;
  background-size: 18px;
  background-position: 95%;
}

.relativedropdown_company{
    position: absolute;
    z-index:999;
    width: 100%;
}
.pp_prnt_1
{
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0,0,0,0.9);
    color: white;
    position: fixed;
    z-index: 999;
    /* transition: opacity 500ms; */
    opacity: 0.6;
}

.pp_cls-invite{
    width: 36%;
    padding: 30px;
    z-index: 999999999;
    top: 50%;
    left: 50%;
    /* opacity: 1 !important; */
    background-color: #fff;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    position: fixed;
    transition: all 5s ease-in-out;
    border: 1px solid white;
}
.mpin-input{
    width: 70%;
    margin: auto;
    margin-top: 30px;
}

.noteGstin{
    font-size: 12px;
    color: #091C3F;
    text-align: center;
    margin-left: -80px;
    margin-top: 7px;
}

@media(max-width: 720px){

    .GSTINContent{
        top: 10%;
    }
    .GSTINSection{
        height: 25%;
        padding-top: 15%;
    }

    .ordive{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .pp_cls-invite{
        width: 90%;
    }
}
