.headOut_CntUs_Cls{
  padding-top: 80px;
  text-align: center;
}
.mainHeadClsCntUs{
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 800;
  font-size: 42px;
  line-height: 51px;
  text-align: center;
  letter-spacing: 0.006em;
  color: #091C3F;
}
.subHeadClsCntUs{
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: -0.0045em;
  color: rgba(9, 28, 63, 0.6);
  width: 480px;
  margin: auto;
  padding-top: 15px;
}
.ContUsSpecOut_CntUs_Cls{
  padding-top: 20px;
  display: flex;
  justify-content: center;
  padding-bottom: 90px;
}
.SpecCntUsDivSec{
  background: #FFFFFF;
  border: 1px solid rgba(9, 28, 63, 0.1);
  box-sizing: border-box;
  border-radius: 5px;
  width: 240px;
  margin-right: 20px;
}
.imgSectCntUsDivCls{
  display: flex;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
}
.headSecCntUsSpec{
  text-align: center;
  font-family: "Gilroy";
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.006em;
  color: #091C3F;
  padding-bottom: 20px;
}
.subHeadSecCntUsSpec{
  font-weight: normal;
  font-size: 14px;
  line-height: 150.6%;
  text-align: center;
  letter-spacing: 0.0045em;
  color: rgba(9, 28, 63, 0.6);
  width: 165px;
  margin: auto;
  padding-bottom: 35px;
}
.MapAddrSecOut_CntUs_Cls{
  background-image: url(https://res.cloudinary.com/bits-pilani/image/upload/v1599572112/New-home-screen/Group_749_i5olie.svg);
  background-size: 100%;
  height: 540px;
  background-color: #D1E4FF;
  background-repeat: no-repeat;
}
.AddSecOutCntUs{
  background-color: white;
  width: 520px;
  margin-left: 5%;
  margin-top: 100px;
  position: absolute;
  padding-left: 30px;
}
.cmpNmCntUsCls{
  padding-top: 30px;
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 800;
  font-size: 22px;
  line-height: 150.6%;
  letter-spacing: 0.0045em;
  color: #091C3F;
  padding-bottom: 15px;
}
.cmpAddrCntUsCls{
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: -0.0045em;
  color: #6B778C;
  width: 80%;
  padding-bottom: 15px;
}
.widthEmptyLineCntUs{
  opacity: 0.5;
  border: 1px solid rgba(9, 28, 63, 0.22);
  width: 95%;
  margin-bottom: 15px;
}
.lblTxtCntUcTxt{
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: -0.0045em;
  color: #6B778C;
  width: 90%;
  padding-bottom: 20px;
}
.malLblCntUs{
  font-weight: normal;
  font-size: 18px;
  line-height: 40px;
  letter-spacing: -0.0045em;
  color: #6B778C;
}
.malIdCntUs{
  margin-left: 10px;
    font-family: "Gilroy";
    text-decoration: underline;
    line-height: 30px;
    cursor: pointer;
}
.lblMailTxtCntUs{
  width: 90%;
  padding-bottom: 40px;
}
.malIdCntUsBtnOuter{
  float: right;
}
.parentOut_Dtls_Cls .footer-mg{
  margin-left: 150px;
}
.imgmobindia{
  display: none;
}

@media(max-width: 720px){
  .subHeadClsCntUs{
    width: 95%;
    margin: auto;
  }

  .mainHeadClsCntUs{
    width: 100%;
    text-align: center;
  }
  .ContUsSpecOut_CntUs_Cls{
   overflow-x: auto;
   justify-content: start;
   margin-right: 20px;
  }

  .SpecCntUsDivSec{
    // width: 240px;
    margin: auto;
    margin: 0px 0px 0px 20px;
    // padding: 0px 10px 0px 10px;
  }

  .imgSectCntUsDivCls{
    width: 230px;
  }

  .AddSecOutCntUs{
    width: 90%;
    margin:auto;
    position: unset;
    padding:20px;
  }

  .MapAddrSecOut_CntUs_Cls{
    background-image:none;
    background-size:unset;
    height: 600px;
  }

  .imgmobindia{
    display: block;
    background-color: #D1E4FF;
  }

  .lblMailTxtCntUs{
    width: 100%;
    // text-align: center;
    // margin-top: 15px;
  }
  .malIdCntUsBtnOuter{
    float: none;
   text-align: center;
   margin-top: 20px;
  }
  .parentOut_Dtls_Cls .footer-mg{
    margin-left: 0px;
    text-align: center;
    margin-bottom: 20px;
  }

}