.summaryMob .main-flx{
    display: flex;
    width:90%;
    margin: auto;
    padding-top: 40px;
    // padding-bottom: 70px;
}
.summaryMob .summary-skele{
    display: block;
    // width: 35%;
    margin-left: 0px;
}
.summaryMob .frst-flx{
    width: 60%;
    margin: auto;
}
.summaryMob .web-view-compo{
    display: block;
}
.summaryMob .mob-view-compo{
    display: none;
}
.summaryMob .mobview-float-cls{
    display: none ;
}

@media screen and (min-width: 1500px){
    .summaryMob .main-flx{
        width: 1200px;
    }
  }
  @media(min-width:730px)and(max-width: 800px){
    .summary-skele .summary-cls{
      left: 25%;
      top: 12%;
    }
  }
  @media(max-width: 720px){
    .summaryMob .summary-skele{
        margin-left: 0px;
        display: block;
    }
    .summaryMob .frst-flx{
        width: 100%;
        margin: auto;
        text-align: center;
    }
    .summaryMob .web-view-compo{
        display: none;
    }
    .summaryMob .mob-view-compo{
        display: block;
    }
    .summaryMob .main-flx{
        width: 100%;
        margin: auto;
        padding-top: 20px;
        padding-bottom: 40px;
    }
    .summaryMob .mobview-float-cls{
        display: block;
        position: fixed;
        top: 90vh;
        text-align: center;
    }
    .summaryMob .mobview-float-cls_old{
      position: fixed;
      width: 100%;
      bottom: 0px;
      background-color: white;
      display: block;
      height: 100px;
      padding-top: 20px;
    }
    .summaryMob .subscribe-btnn{
        border: none;
        outline: none !important;
        width: 270px;
        height: 56px;
        background: #1987FB;
        border-radius: 40px;
        color: white;
        font-size: 18px;
        font-weight: 500;
    }
    .summaryMob .summary-cls{
      position: absolute !important;
    }
    .summaryMob .cmn-sum-cls{

    }
    .mobview-summary-div{
        position: fixed;
        width: 100%;
        background-color: white;
        padding: 0px 0px 20px 0px;
    }
  }
