.ons_head_style_cls{
  display: flex;
  justify-content: center;
}
.dispFlx_head_cls{
  display: flex;
}
.head_blclr_cls{
  color: #1987fb;
}
.mainHeading_head_cls{
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 800;
  font-size: 35px;
  line-height: 124%;
  /* or 52px */
  letter-spacing: 0.005em;
  color: #091C3F;
}
.head_wid70_cls{
  width: 70%;
  margin-top: 10%;
}
.subCnt_head_cls{
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 33px;
  letter-spacing: -0.0045em;
  color: #091C3F;
  margin-top: 20px;
}
.head_out_sec_pad{
  padding-left: 10%;
}
.padRgt_head_cls{
  padding-right: 10%;
}
.imgWidth_head_full_cls{
  width: 80%;
}
.head_img_marT_cls{
  padding-top: 15px;
  padding-bottom: 15px;
    /* position: fixed; */
    /* top: 0%; */
    /* left: 50%; */
    background-color: #fff;
    /* z-index: 999; */
    width: 100%;
    /* box-shadow: 0px 20px 60px rgba(94, 125, 182, 0.1); */
}
.head_cnt_marTp_cls{
  margin-top: 7%;
  padding-bottom: 5%;
}
.imgWeb_mob_cls{
  display: none;
}
.imgWeb_head_cls{
  display: block;
}
@media(max-width: 720px){
  .head_cnt_marTp_cls{
    flex-direction: column-reverse;
  }
  .head_cnt_marTp_cls
  {
    margin-top: 15%;
    padding-left: 10%;
    padding-right: 10%;
    padding-bottom: 10%;
  }
  .ons_head_style_cls{
    box-shadow: 0 10px 30px rgba(94,125,182,.1);
    padding: 15px 0px;
    top: -0.5%;
  }
  .padRgt_head_cls{
    padding-right: 0px;
  }
  .head_out_sec_pad{
    padding-left: 0px;
    text-align: center;
  }
  .head_wid70_cls{
      width: 100%;
  }
  .mainHeading_head_cls{
    font-size: 22px;
    line-height: 124%;
    letter-spacing: 0.005em;
  }
  .subCnt_head_cls{
    font-size: 16px;
    line-height: 20px;
    color: rgba(9, 28, 63, 0.8);
    letter-spacing: -0.0045em;
  }
  .imgWeb_mob_cls{
    display: block;
  }

  .imgWeb_head_cls{
    display: none;
  }

  .imgWidth_head_full_cls{
    width: 100%;
  }
}
