.OrgCardHeading{
    font-size: $HeadingSize;
    font-weight: 700;
    color:$HeadingColor;
}

.OrgCardSubHeading{
    margin: 10px 10px 20px 0px;
    font-size: 15px;
    color: rgba(9, 28, 63, 0.6);
}

.OrgBackArrow{
    border: 1px solid $HeadingColor;
    border-radius: 15px;
    width: 70px;
    height: 28px;
    text-align: center;
    cursor: pointer;
}

.OrgBackArrowDisable{
    border: 1px solid $HeadingColor;
    border-radius: 15px;
    width: 70px;
    height: 28px;
    text-align: center;
    cursor: pointer;
    opacity: 0.2;
}

.OrgNextArrow{
    border: 1px solid $HeadingColor;
    border-radius: 50%;
    text-align: center;
    width: 29px;
    height: 28px;
    margin-left: 10px;
    margin-bottom: 50px;
    cursor: pointer;
}

.OrgDynamicCards{
    padding: 7px;
    width: 280px;
    background-color: #fff;
    min-height: 360px;
    border: 1px solid rgba(9, 28, 63, 0.1);
    border-radius: 5px;
    cursor: pointer;
    margin: 10px 0px 10px 0px;
}

.CustDynamicCards{
    background-color: #fff;
    width: 290px;
    color: $HeadingColor;
    font-size: 14px;
    box-shadow: 0px 10px 26px rgba(94, 125, 182, 0.1);
}

.small_scroll_cust_says{
    height: 180px;
    overflow-y:auto;

    line-height: 126%;
    letter-spacing: 0.005em;
    color: rgba(9, 28, 63, 0.5);
}

.CustHead{
    font-weight: bold;
}

.CustSubHead{
    font-size: 12px;
    text-transform: uppercase;
}

.OrgDynamicCards:hover{
    border: .5px solid #fff;
    box-shadow: 0px 10px 30px rgba(94, 125, 182, 0.1);
    transition: box-shadow 500ms;
}

.pointerclass{
