//Scrollbar styling
/* width */
.small_scroll_cust_says::-webkit-scrollbar {
    width: 4px;
    border-radius: 3px;
  }

  /* Track */
  .small_scroll_cust_says::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  .small_scroll_cust_says::-webkit-scrollbar-thumb {
    background: #1987FB;
    border-radius:20px
  }

  /* Handle on hover */
  .small_scroll_cust_says::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .small_scroll_cust_says {
    overflow-y: hidden;
  }

  .small_scroll_cust_says:hover{
    overflow-y: auto;
  }

